import React,{useEffect,useState} from 'react'
import './CovidCases.css'

const CovidCases=() =>{

    const [data, setdata] = useState([])

    const getCovidData=async()=>{
        try{
        const res=await fetch("https://data.covid19india.org/data.json");
         const data=await res.json();
         setdata(data.statewise)
         document.title("Covid Cases India-DailyNews")
         console.log(data);
    }
        catch(err)
        {
            console.log(err);
        }
    }


    useEffect(() => {
        
        getCovidData();
    }, [])
    return (
        
        <>
       
      
        <div className="container-fliud mt-5">
         <div className="main-heading">

             <h1 className="text-center" style={{ color:"blue",fontWeight:'bold', margin: '35px 0px', marginTop: '90px',textShadow: " 2px 2px 8px grey"}}><span className="font-weight-bold">INDIA </span> COVID-19 CASES</h1>
         </div>
         <div className="table-responsive">
                <table className="table table-hover">
                    <thead className="table-dark">
                        <tr>
                            <th>State</th>
                            <th>Confirmed</th>
                            <th>Total Recovered</th>
                            <th>Total Deaths</th>
                            <th>Active Cases</th>
                           
                        </tr>
                    </thead>
                        <tbody>

                            {
                                data.map((cure,ind)=>{
                                    if((cure.state==="Total")) 
                                    {
                                    return(
                                     
                                    <tr key={ind} style={{fontWeight:'bold',backgroundColor:'yellow'}}>
                                    <td>{cure.state}</td>
                                    <td>{cure.confirmed}</td>
                                    <td>{cure.recovered}</td>
                                    <td>{cure.deaths}</td>
                                    <td>{cure.active}</td>
                                   
                                </tr>
                                       
                                    )
                                }
                                    else if(!(cure.state==="State Unassigned")) 
                                    {
                                    return(
                                     
                                    <tr key={ind}>
                                    <td>{cure.state}</td>
                                    <td>{cure.confirmed}</td>
                                    <td>{cure.recovered}</td>
                                    <td>{cure.deaths}</td>
                                    <td>{cure.active}</td>
                                   
                                </tr>
                                       
                                    )
                                }
                                
                                }
                                )
                            }
                       
                        </tbody>
                </table>
         </div>
        </div>
        
        </>
        
        
    )
}

export default CovidCases;
